
.wrapper {
	margin: auto;
	display: -webkit-flex; /* Safari */
    -webkit-flex-wrap: wrap; /* Safari 6.1+ */
    display: -ms-flexbox;
    display: flex;   
    -ms-flex-wrap: wrap;   
        flex-wrap: wrap;
    -ms-flex-pack: start;
        justify-content: flex-start;
    margin: 10px;
}

.input {
	display: -ms-flexbox;
	display: flex;
	margin: 10px;
	height: 100px;
}

.input input{
	-ms-flex: 1 1;
	    flex: 1 1;
	height: 100;
	text-align: center;
	border-bottom: 2px solid #D9E8F5;
	border-top: 2px solid #D9E8F5;
	border-left: 2px solid #D9E8F5;
	border-right: transparent;
	background: -webkit-gradient(linear, right top, left top, from(#FFFFFF) , to(#D9E8F5));
	background: -webkit-linear-gradient(right, #FFFFFF , #D9E8F5);
	background: -o-linear-gradient(right, #FFFFFF , #D9E8F5);
	background: linear-gradient(to left, #FFFFFF , #D9E8F5);
	font-size: 40px;

}

.analyze{
	border: transparent;
	background-color: #D9E8F5;
	font-size: 40px;
	text-decoration: underline;

}

.loader {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	padding: 20px;
}


